.section {
  height: 690px;
  background: center no-repeat url('../../img/background/eighthScreen.png');
  background-size: cover;
  clip-path: polygon(0% 0%, 100% 5%, 100% 97%, 50% 100%, 0 97%);
  transform: translate(0, -30%);
  padding-top: 110px;
}

@media(min-width: 1200px) {
  .section {
    height: 100vh;
    clip-path: polygon(0% 0%, 100% 10%, 100% 94%, 50% 100%, 0 94%);
    transform: translate(0, -60vh);
    padding-top: 10%;
    position: relative;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30vh;
  }
  .img {
    display: block;
    position: absolute;
    height: auto;
    width: 40vw;
    left: 55%;
    bottom: 5%;
    z-index: -1;
  }
  .something {
    position: absolute;
    z-index: -2;
    left: 0;
    bottom: 0;
    display: block;
    width: 22%;
    height: auto;
  }
}
