.header {
  font-size: 22px;
  font-family: "Caviar Dreams";
  color: #69c9ca;
  text-align: center;
  text-transform: uppercase;
  margin-top: 50px;
  margin-bottom: 30px;
}
.form {
  width: 320px;
  margin: 0 auto;
}
.form input {
  border: 1px solid #fff;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  padding: 15px 20px;
  background: none;
  user-select: initial;
  width: 100%;
}
.form > div {
  margin-bottom: 20px;
}
.form .submit {
  border: 1px solid #69c9ca;
  color: #69c9ca;
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  transition: all 0.3s ease;
}
.submit:hover {
  background: #69c9ca;
  color: #fff;
}

@media(min-width: 1200px) {
  .header {
    font-size: 24px;
    margin-top: 0;
    margin-bottom: 3vh;
    text-align: left;
  }
  .form {
    width: 100%;
  }
  .form > div {
    margin-bottom: 2vh;
  }
  .form input {
    font-size: 16px;
    padding: 1.3vh 1.5vw;
  }
  .form .submit {
    width: 15vw;
    padding: 1.3vh 0;
  }
}
