.header {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Caviar Dreams';
  font-size: 30px;
  line-height: 30px;
}

@media(min-width: 1200px) {
  .header {
    height: 100px;
    line-height: 100px;
    background: url('../../../img/elements/buttonsGroup.png') no-repeat;
    background-position: 40% 100%;
    font-size: 60px;
  }
}
