.section {
  height: 690px;
  background: #f4f5f9;
  background-image: url('../../img/background/teamMobSomething.png');
  background-size: 27% auto;
  background-position: 100% 0;
  background-repeat: no-repeat;
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 97%, 50% 100%, 0 97%);
  transform: translate(0, -39%);
  padding-top: 110px;
}

@media(min-width: 1200px) {
  .section {
    height: 100vh;
    background-image: url('../../img/background/teamDeskSomething.png');
    background-size: auto 31%;
    background-position: 0 0;
    background-repeat: no-repeat;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 94%, 50% 100%, 0 94%);
    transform: translate(0, -78vh);
    padding-top: 10%;
    position: relative;
  }
}
