.section {
  height: 690px;
  background: linear-gradient(#020c14, #003);
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 100%, 0 100%);
  transform: translate(0, -42%);
  padding-top: 110px;
  position: relative;
  padding-top: 55px;
}

.wrapperParticles {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.particles {
  height: 100%;
  width: 100%;
}
.footer {
  position: absolute;
  z-index: 999;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  color: #fff;
  font-size: 12px;
  font-weight: 300;
  text-align: center;
}
@media(min-width: 1200px) {
  .section {
    height: 100vh;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 100%, 0 100%);
    transform: translate(0, -84vh);
    padding-top: 10%;
    position: relative;
  }
  .footer {
    font-size: 14px;
    bottom: 30px;
  }
}
