.section {
  height: 690px;
  background: center no-repeat url('../../img/background/seventhScreen.png');
  background-size: cover;
  clip-path: polygon(0% 0%, 100% 5%, 100% 100%, 0% 95%);
  transform: translate(0, -25%);
  padding-top: 110px;
}

@media(min-width: 1200px) {
  .section {
    height: 100vh;
    clip-path: polygon(0% 0%, 100% 10%, 100% 100%, 0% 90%);
    transform: translate(0, -50vh);
    padding-top: 10%;
    position: relative;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30vh;
  }
  .img {
    display: block;
    position: absolute;
    height: auto;
    width: 30vw;
    left: 55%;
    bottom: 10%;
    z-index: -1;
  }
  .something1 {
    position: absolute;
    width: 18%;
    right: 0;
    top: -7%;
    z-index: -2;
  }
  .something2 {
    position: absolute;
    display: block;
    width: 34%;
    right: 0;
    bottom: 0%;
    z-index: -2;
  }
}
