.section {
  height: 690px;
  background: #fcfafb;
  background-image: url('../../img//background/portfolioMobSomething.png');
  background-position: 0 0;
  background-size: 27% auto;
  background-repeat: no-repeat;
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 97%, 50% 100%, 0 97%);
  transform: translate(0, -36%);
  padding-top: 110px;
}

@media(min-width: 1200px) {
  .section {
    height: 100vh;
    background-image: none;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 94%, 50% 100%, 0 94%);
    transform: translate(0, -72vh);
    padding-top: 10%;
    position: relative;
  }
}
